.hustle-two {
	.wph-preview--eye {
		width: 50px;
		height: 50px;
		cursor: pointer;
		display: block;
		position: absolute;
		top: 60px;
		left: 0;
		margin: 0;
		padding: 0;
		border: 1px solid $color--elephant;
		border-radius: 100%;
		background: $color--astronaut;
		text-align: center;
		transition: transform 0.25s linear;
		.wph-icon {
			position: relative;
			top: 1px;
			color: $color--white;
		}
		&:hover {
			background: $color--elephant;
		}
		// Sticky eye icon
		&.wph-sticky--element {
			position: fixed;
			top: 0;
			left: 210px;
			transform: translateY(92px);
			z-index: 99;
		}
	}
}