// Sprite Helpers
%sprite--dashboard {
	width: 50px;
	height: 50px;
	display: block;
	background: url('../img/wph-dashboard.png') no-repeat transparent;
	background-image: url('../img/wph-dashboard.svg'), none;
}
%sprite--icons {
	background: url('../img/sprite.png') no-repeat transparent;
	background-image: url('../img/sprite.svg'), none;
}
%sprite--listings {
	width: 30px;
	height: 30px;
	display: block;
	background: url('../img/wph-email-providers.png') no-repeat transparent;
	background-image: url('../img/wph-email-providers.svg'), none;
}