.hustle-two .wph-margin {
	&--none, &--nomargin {
		margin: 0 !important;
	}
	&--10 {
		margin: 10px 0;
		&t {
			margin: 10px 0 0;
		}
		&b {
			margin: 0 0 10px;
		}
	}
	&--20 {
		margin: 20px 0;
		&t {
			margin: 20px 0 0;
		}
		&b {
			margin: 0 0 20px;
		}
	}
	&--30 {
		margin: 30px 0;
		&t {
			margin-top: 30px;
			&_less {
				margin-top: -30px;
			}
		}
		&b {
			margin-bottom: 30px;
			&_less {
				margin-bottom: -30px;
			}
		}
	}
	&--40 {
		margin: 40px 0;
		&t {
			margin-top: 40px;
		}
		&b {
			margin-bottom: 40px;
		}
	}
}