.hustle-two.hustle-two {
	.row {
		.col-full .dev-box {
			padding: 0;
			&:last-child {
				margin-bottom: 0;
			}
			.box-title {
				margin: 0;
			}
		}
		.col-half {
			@media (max-width: 960px) {
				width: 100%;
				display: block;
				padding: 0;
			}
		}
	}
}
// TO MOVE
// CUSTOM CONTENT WIZARD » DESIGN TAB
.hustle-two {
	#wph-ccontent--design {
		.wph-flex--box #wph-ccontent--border > .wph-label--picker {
			margin-bottom: 30px;
		}
		.wph-flex--box #wph-ccontent--shadow .wph-box--element .wph-pickers {
			z-index: 1;
		}
	}
	#wph-ccontent--settingstab .wph-ccontent--conditions h4 {
		+ p {
			margin-bottom: 30px;
		}
	}
}

// TO MOVE
#wph-edit-provider-modal {
	position: fixed;
	top: 32px;
	right: 0;
	bottom: 0;
	left: 160px;
	padding: 30px;
	transform-style: preserve-3d;
	.modal-mask {
		width: 100%;
		position: absolute;
		z-index: 99;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background: rgba(51, 51, 51, 0.9);
	}
	.hustle-two .dev-box {
		max-width: 650px;
		position: absolute;
		z-index: 99;
		top: 50%;
		left: 50%;
		margin: 0;
		transform: translate(-50%, -50%);
		.i-close {
			cursor: pointer;
			position: absolute;
			top: 23px;
			right: 30px;
			color: #333;
			&:hover {
				color: #333;
			}
		}
	}
}