.hustle-two {
	select.wpmuiSelect {
		display: none;
	}
	.select2-container {
		width: 100% !important;
	}
	.select2-container .wpmui-select2.select2-selection--single,
	.select2-container .wpmui-select2.select2-selection--multiple {
		min-height: 40px;
		border: 1px solid $color--gainsboro;
		border-radius: 3px;
		background: $color--white;
		color: $color--montana;
		font: 400 15px/18px $font;
	}
	.select2-container .wpmui-select2.select2-selection--single {
		height: 40px;
	}
	.select2-container .wpmui-select2.select2-selection--multiple {
		height: auto;
	}
	.select2-container--default .select2-selection--single .select2-selection__rendered {
		padding: 10px 15px;
		color: $color--montana;
		font: 400 15px/18px $font;
	}
	.select2-container--default .select2-selection--single .select2-selection__arrow {
		width: 15px;
		height: 14px;
		top: 12px;
		right: 15px;
		&:before {
			content: $i-burger;
			color: $color--iron;
			font-size: 15px;
			line-height: 15px;
			font-family: $font--icon;
		}
		b {
			display: none !important;
		}
	}
}

.wp-admin .select2-container .select2-dropdown.wpmui-select2 {
	margin-top: -3px;
	border: 1px solid $color--gainsboro;
	border-top: 0;
	border-radius: 0 0 3px 3px;
	box-shadow: none;
}
.wp-admin .select2-container .select2-dropdown.wpmui-select2 .select2-search {
	display: none;
}
.wp-admin .select2-container .select2-dropdown.wpmui-select2 .select2-results .select2-results__option {
	padding: 6px 15px;
}
.select2-container .select2-dropdown.wpmui-select2 .select2-results,
.select2-container .select2-dropdown.wpmui-select2 .select2-results .select2-results__option[aria-selected=true] {
	color: $color--montana;
	font: 300 14px/18px $font;
}
.wp-admin .select2-container .select2-dropdown.wpmui-select2 .select2-results .select2-results__option--highlighted,
.wp-admin .select2-container .select2-dropdown.wpmui-select2 .select2-results .select2-results__option--highlighted[aria-selected=true],
.wp-admin .select2-container--default .select2-results__option--highlighted[aria-selected] {
	background: $color--gainsboro;
	color: $color--montana;
	font: 300 14px/18px $font;
}
.wp-admin .select2-container .select2-dropdown.wpmui-select2 .select2-results .select2-results__option:before {
	display: none;
}