// Google Fonts
@import url('https://fonts.googleapis.com/css?family=Roboto+Condensed:300,400,700|Roboto:300,400,500,700&subset=cyrillic,cyrillic-ext,greek,greek-ext,latin-ext,vietnamese');
@import url("https://fonts.googleapis.com/css?family=Source+Code+Pro");

// Custon Fonts
$hustle-font-path: "../icons" !default;

@font-face {
	font-family: 'hustle';
	src:  url('#{$hustle-font-path}/hustle.eot?hk72z');
	src:  url('#{$hustle-font-path}/hustle.eot?hk72z#iefix') format('embedded-opentype'),
		  url('#{$hustle-font-path}/hustle.ttf?hk72z') format('truetype'),
		  url('#{$hustle-font-path}/hustle.woff?hk72z') format('woff'),
		  url('#{$hustle-font-path}/hustle.svg?hk72z#hustle') format('svg');
	font-weight: normal;
	font-style: normal;
}

.wph-icon {
	/* use !important to prevent issues with browser extensions that change fonts */
	font-family: 'hustle';
	speak: none;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;
	
	/* Better Font Rendering =========== */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

