.hustle-two label {
	display: block;
	padding: 7px 10px;
	font-weight: 500;
	font-family: 'Roboto', sans-serif;
	font-size: 15px;
	margin: 0 0 4px;
	color: #555;
}
.hustle-two label.inline-label {
	display: inline-block;
	margin: 0;
	padding: 0;
	font-weight: inherit;
	font-size: inherit;
}
.hustle-two form > div {
	padding-top: 5px;
}
.hustle-two form > div:first-child,
.hustle-two form > div.buttons {
	padding-top: 0;
}

.hustle-two label {
	cursor: text;
	&.wpoi-label-bold {
		margin-bottom: 10px;
		padding: 0;
		color: #777;
		font: bold 14px/16px "Roboto Condensed", "Roboto", sans-serif;
		letter-spacing: -0.2px;
		text-transform: uppercase;
	}
}

.hustle-two .wpoi-wrapper {
	label {
		margin: 0;
		padding: 0;
		display: inline-block;
		color: #777771;
		font: 400 15px/20px "Roboto", sans-serif;
		strong {
			font-weight: bold;
		}
		span {
			opacity: 0.5;
		}
	}
}

// ============================================== //
// ================= HUSTLE 2.0 ================= //
// ============================================== //
// ======== by Leighton Sapir (WPMU DEV) ======== //
// ============================================== //

.hustle-two.hustle-two {
	label {
		margin: 0 0 5px;
		padding: 0;
		color: $color--montana;
		font: 400 15px/18px $font;
	}
	label.wph-label--alt {
		margin: 0 0 15px;
		padding: 0;
		color: $color--montana;
		font: 400 15px/18px $font;
		&.wph-label--border {
			margin: 0 0 30px;
			padding: 0 0 25px;
			border-bottom: 1px solid $color--lilac;
		}
		span {
			color: $color--shaft;
		}
		span + small {
			display: block;
		}
	}
	label.wph-label--legend {
		margin: 0 0 15px;
		padding: 0;
		color: $color--montana;
		font: 400 12px/18px $font--alt;
		letter-spacing: -0.15px;
		&:last-child {
			margin-bottom: 0;
		}
	}
	label.wph-label--module {
		margin: 0;
		padding: 0;
		color: $color--shaft;
		font: 700 14px/70px $font--alt;
		letter-spacing: -0.15px;
	}
	label.wph-label--provider {
		opacity: 0.4;
		margin: 0;
		padding: 0;
		color: $color--shaft;
		font: 400 14px/70px $font;
		letter-spacing: -0.15px;
	}
	label.wph-label--notice {
		height: auto;
		opacity: 1;
		visibility: visible;
		margin: 0 0 15px;
		padding: 13px 15px;
		border-radius: 3px;
		background: $color--bleach;
		color: $color--notice;
		font: 400 12px/14px $font;
		letter-spacing: -0.15px;
		transition: height 0.3s linear, opacity 0.3s ease;
		span {
			opacity: 0.9;
		}
		&.disabled {
			height: 0;
			opacity: 0;
			visibility: hidden;
			margin: 0;
			padding: 0;
		}
	}
	// Block Label
	.wph-label--block {
		label {
			margin-bottom: 5px;
		}
	}
	// Inline Label
	.wph-label--checkbox {
		position: relative;
		label {
			margin: 0;
			cursor: pointer;
		}
		> label {
			display: block;
			padding: 2px 0 0 35px;
			line-height: 20px;
		}
		label + input[type="checkbox"],
		label + .wph-input--checkbox {
			position: absolute;
			top: 0;
			left: 0;
			margin: 0;
		}
		label + .wph-input--checkbox {
			top: 1px;
		}
		&.inline {
			height: 22px;
			display: inline-block;
		}
		&.margin {
			margin-bottom: 20px;
		}
	}
	.wph-label--radio {
		position: relative;
		> label {
			display: block;
			padding: 2px 0 0 35px;
			line-height: 20px;
		}
		> label + input[type="radio"],
		> label + .wph-input--radio {
			position: absolute;
			top: 1px;
			left: 0;
			margin: 0;
		}
	}
	.wph-label--number {
		position: relative;
		margin-bottom: 15px;
		&:last-child {
			margin-bottom: 0;
		}
		label {
			display: inline-block;
			margin: 0 0 0 10px;
			line-height: 40px;
		}
		label + .wph-input--number {
			float: left;
			display: inline-block;
			position: relative;
		}
		&.wph-label--left {
			label {
				margin: 0;
			}
			label + .wph-input--number {
				float: none;
				margin-left: 10px;
			}
		}
	}
	.wph-label--select {
		position: relative;
		label {
			display: block;
		}
	}
	.wph-label--picker {
		position: relative;
		margin-bottom: 15px;
		&:last-child {
			margin-bottom: 0;
		}
		label {
			display: block;
			padding: 2px 0 0 40px;
			line-height: 30px;
		}
		label + .wph-pickers {
			position: absolute;
			top: 1px;
			left: 0;
		}
	}
	.wph-label--toggle {
		position: relative;
		label:not(.toggle-label) {
			display: block;
			padding-left: 50px;
			line-height: 22px;
		}
		label:not(.toggle-label) + .toggle {
			position: absolute;
			top: -1px;
			left: 0;
		}
	}
	.wph-label--mix {
		position: relative;
		> label {
			display: inline-block;
			margin: 0;
			padding: 2px 10px 0 35px;
			line-height: 38px;
		}
		> label + input[type="radio"],
		> label + .wph-input--radio,
		> label + .wph-input--checkbox {
			position: absolute;
			top: 11px;
			left: 0;
			margin: 0;
		}
		> label + input[type="radio"] + .wph-input--number,
		> label + .wph-input--radio + .wph-input--number,
		> label + .wph-input--checkbox + .wph-input--number {
			display: inline-block;
			position: relative;
			top: 1px;
			margin-right: 10px;
		}
		> label + input[type="radio"] + .wph-input--number + label,
		> label + .wph-input--radio + .wph-input--number + label,
		> label + .wph-input--checkbox + .wph-input--number + label {
			padding: 0 10px 0 0;
		}
		> label + input[type="radio"] + .wph-input--number + select + .select2-container,
		> label + .wph-input--radio + .wph-input--number + select + .select2-container,
		> label + .wph-input--checkbox + .wph-input--number + select + .select2-container {
			max-width: 200px;
			display: inline-block;
			position: relative;
			vertical-align: middle;
		}
	}
	// Special Checkbox
	.wph-label--special_checkbox {
		display: flex;
		margin-bottom: 10px;
		border-radius: 10px;
		label {
			flex: 1;
			cursor: pointer;
			padding: 5px;
			border: 1px solid $border--crystal;
			border-left: 0;
			border-radius: 10px;
			background: $color--haze;
			line-height: 16px;
			text-align: center;
			transition: 0.2s ease;
			&:hover {
				border-color: $color--manatee;
				background: $color--manatee;
				.wph-icon {
					color: $color--white;
				}
			}
			&:first-of-type {
				border-left: 1px solid $border--crystal;
				border-radius: 10px 0 0 10px;
			}
			&:last-of-type {
				border-radius: 0 10px 10px 0;
			}
			.wph-icon {
				color: $color--manatee;
				&.i-text {
					opacity: 0.4;
				}
				&:first-child {
					margin-right: 5px;
				}
			}
		}
		input {
			display: none;
			&:checked + label {
				border-color: $color--manatee;
				background: $color--manatee;
				.wph-icon {
					color: $color--white;
				}
			}
		}
	}
}