.hustle-two .wph-accordions {
	.wph-accordions--item {
		margin-bottom: 10px;
		header {
			position: relative;
			margin-bottom: 1px;
			padding: 0 30px;
			cursor: pointer;
			background: $color--white;
			.toggle {
				margin-right: 10px;
			}
			.wph-label--module,
			.wph-label--provider {
				display: inline-block;
			}
			.wph-label--provider {
				margin-left: 5px;
			}
			.wph-accordion--buttons {
				display: inline-block;
				vertical-align: middle;
				margin-left: 30px;
				.wph-button {
					float: left;
					display: block;
					margin-right: 5px;
					&:before {
						@media (max-width: 780px) {
							display: block;
							font-family: $font--icon;
						}
					}
					&:last-child {
						margin-right: 0;
					}
					@media (max-width: 780px) {
						width: 40px;
						height: 30px;
						overflow: hidden;
						padding: 0;
					}
				}
				.wph-button--gray:before {
					@media (max-width: 780px) {
						content: $i-edit;
					}
				}
				.hustle-delete-optin,
				.custom-content-delete {
					&:before {
						@media (max-width: 780px) {
							content: $i-trash;
						}
					}
					@media (max-width: 580px) {
						display: none;
					}
				}
				.hustle-delete-module-confirm:before {
					@media (max-width: 780px) {
						content: $i-check;
					}
				}
				.hustle-delete-module-cancel:before {
					@media (max-width: 780px) {
						content: $i-close;
					}
				}
				@media (max-width: 780px) {
					margin-left: 10px;
				}
			}
			.button-view-email-list {
				position: absolute;
				top: 20px;
				right: 60px;
				@media (max-width: 780px) {
					width: 40px;
					height: 30px;
					overflow: hidden;
					padding: 0;
					&:before {
						content: $i-mail;
						display: block;
						font-family: $font--icon;
					}
				}
			}
			&.wph-accordion--animate_buttons {
				.wph-accordion--buttons {
					opacity: 0;
					transition: 0.2s linear;
				}
				&:hover .wph-accordion--buttons {
					opacity: 1;
				}
			}
			.accordion-state {
				cursor: pointer;
				position: absolute;
				top: 40%;
				right: 30px;
			}
			.hustle-delete-module-confirmation {
				float: right;
				display: inline-block;
				margin-left: 20px;
				label {
					float: left;
					display: inline-block;
					margin: 0 5px 0 0;
					line-height: 30px;
					@media (max-width: 780px) {
						display: none;
					}
				}
				@media (max-width: 780px) {
					margin-left: 5px;
				}
			}
		}
		section {
			display: none;
			position: relative;
			background: $color--haze;
			transform-style: preserve-3d;
			&.wph-accordion--padding {
				padding: 30px;
			}
			.wph-accordion--disable {
				width: 100%;
				height: 100%;
				position: absolute;
				z-index: 9;
				background: $color--haze;
				transform-style: preserve-3d;
				p {
					display: block;
					position: absolute;
					top: 50%;
					right: 0;
					left: 0;
					padding: 0 30px;
					transform: translateY(-50%);
					text-align: center;
				}
			}
			.wph-flex--stats {
				padding-bottom: 0;
			}
			.wph-label--alt {
				margin-bottom: 5px;
			}
			textarea {
				height: 118px;
			}
		}
		&.wph-accordion--open {
			section {
				display: block;
			}
		}
		&:last-child {
			margin-bottom: 0;
		}
	}
	.wph-accordions--item.wph-accordion--closed {
		margin-bottom: 2px;
		header {
			.accordion-state .i-arrow {
				display: block;
				transform: rotate(180deg);
			}
		}
	}
}