.wph-modal.wph-modal--cabriolet {
	header {
		min-height: 24px;
		position: relative;
		h2.wph-modal--title,
		h4.wph-modal--subtitle {
			color: $white;
			text-align: left;
			padding-right: 30px;
		}
		.wph-modal--close {
			position: absolute;
			right: 0;
			bottom: 0;
			margin-bottom: 0;
		}
	}
	section {
		display: flex;
		flex-wrap: wrap;
		//border: 3px solid $gray;
		border-radius: 5px;
		background: $white;
		.wph-modal--image {
			flex: 0 200px;
			&_full {
				flex: 1;
			}
		}
		.wph-modal--message {
			flex: 1;
			padding: 20px;
		}
		.wph-modal--clear {
			margin-top: 15px;
			&:not(:first-child) {
				margin: 15px 0 0;
			}
			a:not(:first-child) {
				margin-left: 20px;
			}
			a:not(.wph-modal--cta) {
				color: $green;
				&:hover {
					color: $green-dark;
				}
			}
		}
		// Image: Align to left
		&.wph-modal--image_left {
			flex-direction: row;
			@media (max-width: 770px){
				flex-direction: column;
			}
		}
		// Image: Align to right
		&.wph-modal--image_right {
			flex-direction: row-reverse;
			@media (max-width: 770px){
				flex-direction: column-reverse;
			}
		}
	}
}

// Image: Border Radius
.wph-modal.wph-modal--cabriolet {
	.wph-modal--content {
		// Image: Left
		.wph-modal--image_left {
			.wph-modal--image,
			.wph-modal--image img {
				border-top-left-radius: inherit;
				@media (min-width: 771px) {
					border-bottom-left-radius: inherit;
				}
				@media (max-width: 770px) {
					border-top-right-radius: inherit;
				}
			}
		}
		// Image: Right
		.wph-modal--image_right {
			.wph-modal--image,
			.wph-modal--image img {
				border-bottom-right-radius: inherit;
				@media (min-width: 771px) {
					border-top-right-radius: inherit;
				}
				@media (max-width: 770px) {
					border-bottom-left-radius: inherit;
				}
			}
		}
		// Image: Full
		.wph-modal--image_left,
		.wph-modal--image_right {
			.wph-modal--image_full,
			.wph-modal--image_full img {
				border-radius: inherit;
			}
		}
	}
}