.hustle-two {
	#header {
		padding: 0 15px;
	}
	h1, h2, h3, h4, h5, h6 {
		display: block;
		margin: 0;
		padding: 0;
		line-height: 1em;
		color: $color--montana;
	}
	h1, h2, h3, h4 {
		font-family: $font--alt;
	}
	h1, h2, h3, h4 {
		font-weight: 700;
		text-transform: uppercase;
	}
	h5, h6 {
		font-family: $font;
		font-weight: 400;
	}
	// Heading 1
	h1 {
		margin: 30px 0;
		color: $color--shaft;
		font-size: 30px;
		line-height: 40px;
		letter-spacing: -0.6px;
	}
	.no-margin-btm h1 {
		margin-bottom: 0;
	}
	// Heading 2
	h2 {
		margin: 0;
		color: $color--steel;
		font-size: 22px;
		line-height: 60px;
		letter-spacing: -0.5px;
	}
	// Heading 3
	h3, .dev-box .box-title h3 {
		color: $color--shaft;
		font-size: 18px;
		line-height: 60px;
		letter-spacing: -0.45px;
	}
	// Heading 4
	h4 {
		margin: 0;
		color: $color--shaft;
		font-size: 14px;
		line-height: 70px;
		letter-spacing: -0.15px;
	}
	// Heading 5
	h5, .wph-flex--title h5 {
		margin: 0;
		color: $color--shaft;
		font-size: 15px;
		line-height: 18px;
	}
	#wph-ccontent--popup .wph-flex--title h5,
	#wph-ccontent--slidein .wph-flex--title h5,
	#wph-ccontent--magicbar .wph-flex--title h5 {
		line-height: 30px;
	}
	// Heading 6
	h6 {
		color: $color--montana;
		font-size: 16px;
		line-height: 26px;
	}
	// Heading + Text Aligning
	h1, h2, h3, h4, h5, h6,
	.wph-toggletabs--title {
		&.wph-text--center {
			text-align: center;
			@media (max-width: 480px) {
				text-align: left;
			}
		}
		&.wph-border--top {
			border-top: 1px solid $color--lilac;
		}
	}
}