#wpoi-complete-message,
#wpoi-emails-list-modal {
	position: fixed;
	z-index: 99;
	top: 0;
	right: 0;
	bottom: 0;
	left: 160px;
	margin-top: 32px;
	transition: none;
	@include aligncontainer();
	.wpoi-complete-mask {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background: rgba(51,51,51,0.9);
	}
	.wpoi-complete-wrap {
		width: 100%;
		max-width: 90%;
		@include absolutealign(both);
		@media (min-width: 961px) {
			max-width: 600px;
		}
	}
	@media (max-width: 960px) {
		left: 36px;
	}
	@media (max-width: 782px) {
		left: 0;
	}
}
.wp-responsive-open + script {
	& + #wpoi-complete-message,
	& + #wpoi-emails-list-modal {
		@media (max-width: 782px) {
			left: 190px;
		}
	}
}
.wpoi-success-table {
	width: 100%;
	margin-bottom: 30px;
	border-collapse: collapse;
	border-spacing: 0;
	th {
		padding: 0 0 10px 0;
		border-bottom: 1px solid #DEE1E5;
		color: #777771;
		font: bold 15px/18px "Roboto", sans-serif;
		text-align: center;
		&:first-child {
			text-align: left;
		}
	}
	td {
		padding: 10px 0;
		border-bottom: 1px solid #DEE1E5;
		text-align: center;
		&:first-child {
			text-align: left;
		}
		.success-settings-list {
			color: #777;
			font: bold 14px/16px "Roboto Condensed", "Roboto", sans-serif;
			letter-spacing: -0.2px;
			text-transform: uppercase;
		}
	}
	tr:last-child td {
		padding-bottom: 0;
		border-bottom: 0;
	}
}
#wpoi-emails-list-modal {
	opacity: 0;
	visibility: hidden;
	transition: 0.2s ease;
	&.show {
		opacity: 1;
		visibility: visible;
		.wpoi-complete-wrap {
			opacity: 1;
			transform: translate(-50%, -50%);
		}
	}
	.wpoi-complete-wrap {
		opacity: 0;
		transform: translate(-50%, 20%);
		transition: all 0.3s;
	}
	.content-box {
		margin: 0;
		padding: 0;
	}
	.box-title {
		height: auto;
		position: relative;
		margin: 0;
		h3 {
			padding-right: 22px;
			color: #333;
			font: 700 18px/70px "Roboto Condensed", "Roboto", Arial, sans-serif;
			letter-spacing: -0.45px;
			span.wpoi-total-subscribers {
				margin-left: 15px;
				color: #777771;
				font-size: 16px;
				font-family: "Roboto", Arial, sans-serif;
				font-weight: 400;
				text-transform: none;
				@media (max-width: 600px) {
					display: none;
				}
			}
		}
		p {
			color: #777771;
			font: 400 16px/26px "Roboto", sans-serif;
		}
	}
	.inc-opt-close-emails-list {
		width: 22px;
		height: 22px;
		position: absolute;
		top: 25px;
		right: 30px;
		color: #777771;
		&:hover {
			color: #333;
		}
		&:before {
			line-height: 22px;
			text-align: center;
			font-size: 14px;
			-webkit-font-smoothing: antialiased;
			-moz-osx-font-smoothing: grayscale;
			position: absolute;
			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
			transition: color 0.3s;
		}
	}
	.wpoi-emails-list {
		.wpoi-emails-list-header {
			display: flex;
			padding: 0 30px;
			border-bottom: 1px solid #EAEAEA;
			background: #FAFAFA;
			.wpoi-list-fname,
			.wpoi-list-lname {
				flex: 1;
			}
			.wpoi-list-email {
				flex: 2;
			}
			> div {
				padding: 5px 0;
			}
			@media (max-width: 600px) {
				display: none;
			}
		}
		#wpoi-emails-list-content {
			max-height: 300px;
			overflow-x: hidden;
			overflow-y: auto;
			padding: 5px 30px;
			border-bottom: 1px solid #EAEAEA;
			.wpoi-emails-list-subscriber {
				display: flex;
				.wpoi-list-fname,
				.wpoi-list-lname,
				.wpoi-list-email {
					overflow: hidden;
					text-overflow: ellipsis;
					word-break: break-word;
					white-space: nowrap;
					&:before {
						@media (max-width: 600px) {
							content: attr(data-title) ":";
							margin-right: 4px;
							font-weight: bold;
						}
						@media (max-width: 500px) {
							display: block;
						}
					}
				}
				.wpoi-list-fname,
				.wpoi-list-lname {
					flex: 1;
				}
				.wpoi-list-email {
					flex: 2;
					@media (max-width: 600px) {
						flex: 1;
					}
				}
				> div {
					padding: 5px 0;
					font-size: 12px;
					line-height: 14px;
				}
				&:not(:last-child) {
					@media (max-width: 600px) {
						margin-bottom: 10px;
						padding-bottom: 10px;
						border-bottom: 1px solid #EAEAEA;
					}
				}
				@media (max-width: 600px) {
					flex-direction: column;
				}
			}
		}
		.wpoi-emails-list-footer {
			padding: 10px 30px;
			.wph-button {
				width: 100px;
				display: block;
				margin: 0 0 0 auto;
				@media (max-width: 480px) {
					width: 100%;
					margin: 0 auto;
					text-align: center;
				}
			}
		}
	}
}
body.wp-admin.folded {
	#wpoi-complete-message,
	#wpoi-emails-list-modal {
		@media (min-width: 961px) {
			left: 36px;
		}
	}
}