// Row
.hustle-two {
	.row, .row-sep {
		clear: both;
		margin: 0 0 0 0;
		position: relative;
		display: table;
		width: 100%;
		table-layout: fixed;
		&:after {
			content: "";
			display: table;
			clear: both;
		}
	}
	.row {
		.col-third,
		.col-two-third,
		.col-half {
			display: table-cell;
			padding: 0 15px 0 15px;
			position: relative;
			vertical-align: top;
			&:first-child {
				padding-left: 0;
			}
			&:last-child {
				padding-right: 0;
			}
		}
		.col-third {
			width: 33.3333%;
		}
		.col-two-third {
			width: 66.6666%;
		}
		.col-half {
			width: 50%;
		}
	}
	.row-sep {
		height: 1px;
		margin: 0 0 30px;
		width: auto;
		display: block;
		&:before {
			content: '';
			display: block;
			position: absolute;
			left: 0;
			right: 0;
			bottom: 0;
			border-bottom: 1px solid #E5E5E5;
		}
	}
	.row-space {
		margin: 0 0 30px;
	}
}

// Flex Grid
.hustle-two .wph-flex {
	display: flex;
	flex-wrap: wrap;
	position: relative;
	// Flex Grid: Background Variations
	&.wph-flex--gray {
		border-radius: 5px;
		background: $color--storm;
	}
	&.wph-flex--dark {
		border-radius: 5px;
		background: $color--dove;
	}
	// Flex Grid: Flex Direction
	&.wph-flex--row {
		flex-direction: row;
		@media (max-width: 620px) {
			flex-direction: column;
		}
	}
	&.wph-flex--column {
		flex-direction: column;
	}
	&.wph-flex--row_reverse {
		flex-direction: row-reverse;
		@media (max-width: 620px) {
			flex-direction: column-reverse;
		}
	}
	&.wph-flex--column_reverse {
		flex-direction: column-reverse;
	}
	// Flex Grid: Alignment
	&.wph-flex--start {
		justify-content: flex-start;
	}
	&.wph-flex--center {
		justify-content: center;
	}
	&.wph-flex--end {
		justify-content: flex-end;
	}
}

// Flex Element
.hustle-two .wph-flex {
	figure {
		margin: 0;
	}
	&.wph-flex--row >,
	&.wph-flex--column >,
	&.wph-flex--row_reverse >,
	&.wph-flex--column_reverse > {
		.wph-flex--box {
			flex: 1;
		}
		.wph-flex--box,
		.wph-flex--side,
		.wph-flex--side_50,
		.wph-flex--side_100,
		.wph-flex--side_150,
		.wph-flex--side_162,
		.wph-flex--side_210,
		.wph-flex--side_374 {
			img {
				width: 100%;
				height: auto;
				display: block;
			}
		}
	}
	&.wph-flex--row >,
	&.wph-flex--row_reverse > {
		.wph-flex--box,
		.wph-flex--side,
		.wph-flex--side_50,
		.wph-flex--side_100,
		.wph-flex--side_150,
		.wph-flex--side_162,
		.wph-flex--side_210,
		.wph-flex--side_374 {
			// Flex Element: Alignment
			&.wph-flex--start {
				align-self: flex-start;
			}
			&.wph-flex--center {
				align-self: center;
			}
			&.wph-flex--end {
				align-self: flex-end;
			}
		}
		.wph-flex--side {
			flex: 0 180px;
			&_50 {
				flex: 0 50px;
				@media (max-width: 620px) {
					flex: 1;
				}
			}
			&_100 {
				flex: 0 100px;
				@media (max-width: 620px) {
					flex: 1;
				}
			}
			&_150 {
				flex: 0 150px;
				@media (max-width: 620px) {
					flex: 1;
				}
			}
			&_162 {
				flex: 0 162px;
				@media (max-width: 1020px) {
					flex: 0 140px;
				}
				@media (max-width: 620px) {
					flex: 1;
				}
			}
			&_210 {
				flex: 0 210px;
				@media (max-width: 620px) {
					flex: 1;
				}
			}
			&_374 {
				flex: 0 374px;
				@media (max-width: 1020px) {
					flex: 0 240px;
				}
				@media (max-width: 620px) {
					width: 100%;
					flex: 1;
				}
			}
			@media (max-width: 620px) {
				flex: 1;
			}
		}
		.wph-flex--box,
		.wph-flex--side,
		.wph-flex--side_50,
		.wph-flex--side_100,
		.wph-flex--side_150,
		.wph-flex--side_162,
		.wph-flex--side_210,
		.wph-flex--side_374 {
			margin-right: 30px;
			&:last-child {
				margin-right: 0;
				@media (max-width: 620px) {
					margin-bottom: 0;
				}
			}
			@media (max-width: 620px) {
				margin-right: 0;
				margin-bottom: 30px;
			}
		}
	}
	&.wph-flex--column >,
	&.wph-flex--column_reverse > {
		.wph-flex--side {
			flex: 1;
			&_50 {
				flex: 1;
			}
			&_100 {
				flex: 1;
			}
			&_150 {
				flex: 1;
			}
			&_210 {
				flex: 1;
			}
			&_374 {
				flex: 1;
			}
		}
		.wph-flex--box,
		.wph-flex--side,
		.wph-flex--side_50,
		.wph-flex--side_100,
		.wph-flex--side_150,
		.wph-flex--side_162,
		.wph-flex--side_210,
		.wph-flex--side_374 {
			margin-bottom: 30px;
			&:last-child {
				margin-bottom: 0;
			}
		}
	}
}

// Flex Header
.hustle-two .wph-flex {
	&.wph-flex--header {
		margin: 0;
		padding: 0;
		.wph-flex--stats {
			padding: 0;
		}
		label, .wph-label--alt {
			margin-bottom: 0;
		}
		.wph-label--toggle {
			label {
				line-height: 30px;
			}
			label:not(.toggle-label) + .toggle {
				top: 2px;
			}
		}
	}
}

// Flex Grid: Animated
.hustle-two .wph-flex {
	&.wph-flex--animated {
		height: 0;
		opacity: 0;
		overflow: hidden;
		visibility: hidden;
		position: absolute;
		transition: height 0.3s linear, opacity 0.3s linear;
		&.open {
			height: auto;
			opacity: 1;
			overflow: visible;
			visibility: visible;
			position: relative;
		}
	}
}















































