.hustle-two {
	
	// Flex Animation
	.wph-flex.wph-flex--animated {
		
	}
	
	// Flex Grid: Reset
	.wph-flex .wph-box {
		margin: 0;
	}
	
}

.hustle-two .wph-flex {
	// Flex: Border
	&.wph-flex--border {
		margin-bottom: 25px;
		padding-bottom: 30px;
		border-bottom: 1px solid $color--lilac;
		&:last-child {
			margin: 0;
			padding: 0;
			border: 0;
		}
	}
	
	// Flex: Align Item
	.wph-flex--image,
	.wph-flex--side,
	.wph-flex--stats,
	.wph-flex--box {
		&.wph-flex--border {
			margin-bottom: 25px;
			padding-bottom: 30px;
			border-bottom: 1px solid $color--lilac;
		}
	}
	.wph-flex--box {
		&.wph-flex--border {
			margin-bottom: 0;
		}
	}
}

.hustle-two .wph-box {
	display: block;
	margin-bottom: 30px;
	&:before, &:after {
		content: " ";
		display: table;
	}
	&:after {
		clear: both;
	}
	&.wph-box--gray {
		padding: 20px 30px;
		border-radius: 5px;
		background: $color--storm;
	}
	.wph-box--element {
		max-width: 150px;
		float: left;
		margin-right: 30px;
		&:last-child {
			margin-right: 0;
		}
	}
}

.hustle-two .wph-flow {
	display: flex;
	margin-bottom: -30px;
	.wph-flow--image,
	.wph-flow--side,
	.wph-flow--content,
	.wph-flow--box {
		&.wph-flow--start {
			align-self: flex-start;
		}
		&.wph-flow--center {
			align-self: center;
		}
		&.wph-flow--end {
			align-self: flex-end;
		}
	}
	.wph-flow--image {
		flex: 0 162px;
		margin: 0 40px 0 10px;
		img {
			width: 100%;
			height: auto;
			display: block;
		}
	}
	.wph-flow--side {
		flex: 0 374px;
		padding-bottom: 30px;
		&.wph-flow--sideLeft {
			margin-right: 30px;
		}
		&.wph-flow--sideRight {
			margin-left: 30px;
		}
	}
	.wph-flow--box {
		flex: 1;
	}
	.wph-flow--content {
		min-height: 185px;
		flex: 1;
		margin-right: 30px;
		padding-bottom: 30px;
		&:last-child {
			margin-right: 0;
		}
	}
}