.button-spinner{
  background: url(../img/loading.gif) no-repeat;
  -webkit-background-size: 16px 16px;
  background-size: 16px 16px;
  display: inline-block;
  float: right;
  vertical-align: middle;
  opacity: 0.7;
  filter: alpha(opacity=70);
  width: 20px;
  height: 20px;
  margin: 0;
  position: absolute;
  right: 8px;
  top:30%;
}