.hustle-two.hustle-two .wph-table {
	width: 100%;
	border-collapse: collapse;
	thead {
		tr th {
			padding: 0;
			color: $color--shaft;
			font: 700 14px/40px $font--alt;
			letter-spacing: -0.15px;
			text-transform: uppercase;
			text-align: left;
		}
		@media (max-width: 480px) {
			display: none;
		}
	}
	tbody {
		tr th,
		tr td {
			position: relative;
			padding: 15px 0;
			border-top: 1px solid $color--lilac;
			@media (max-width: 480px) {
				width: 100%;
				display: block;
				padding: 0;
				border-top: 0;
			}
		}
		tr th {
			color: $color--shaft;
			font: 700 15px/28px $font--alt;
			letter-spacing: -0.16px;
			text-transform: uppercase;
			text-align: left;
		}
		tr td {
			span.toggle {
				width: 34px;
				display: block;
				margin: 0 auto;
				@media (max-width: 480px) {
					display: inline-block;
				}
			}
			.wph-button {
				min-width: 55px;
				display: block;
				margin: 0 0 0 auto;
				text-align: center;
				@media (max-width: 480px) {
					width: 100%;
					margin: 0 auto;
				}
			}
			> label {
				display: block;
				margin: 0;
				padding: 2px 0 0 35px;
				color: $color--montana;
				font: 400 15px/18px $font;
				+ input[type="checkbox"] {
					position: absolute;
					top: 15px;
					left: 0;
					margin: 0;
					@media (max-width: 480px) {
						top: 0;
					}
				}
				@media (max-width: 480px) {
					padding-top: 0;
					line-height: 20px;
				}
			}
			.wph-table--title,
			.wph-table--subtitle {
				color: $color--montana;
				display: block;
			}
			.wph-table--title {
				font: 400 16px/26px $font;
			}
			.wph-table--subtitle {
				font: 400 12px/16px $font;
				@media (max-width: 480px) {
					overflow: hidden;
					white-space: nowrap;
					text-overflow: ellipsis;
				}
			}
		}
		&.wph-tbody--reset {
			tr td,
			tr th {
				border-top: 0;
				border-bottom: 1px solid $color--lilac;
			}
			tr:last-child td,
			tr:last-child th {
				border-bottom: 0;
			}
		}
		@media (max-width: 480px) {
			width: 100%;
			display: block;
		}
	}
	tfoot {
		tr {
			td {
				padding: 30px 0 0;
				border-top: 1px solid $color--lilac;
				@media (max-width: 480px) {
					width: 100%;
					display: block;
					padding: 15px 0 0;
					border-top: 0;
				}
				.wph-button {
					width: 100px;
					display: block;
					margin: 0 0 0 auto;
					@media (max-width: 480px) {
						width: 100%;
						margin: 0 auto;
					}
				}
			}
			@media (max-width: 480px) {
				width: 100%;
				display: block;
			}
		}
		@media (max-width: 480px) {
			width: 100%;
			display: block;
		}
	}
	// Dashboard Stats
	&.wph-dashboard--stats {
		tbody tr {
			&:first-child {
				td, th {
					padding-top: 0;
					border-top: 0;
				}
			}
			td {
				text-align: right;
			}
			&:last-child {
				td, th {
					padding-bottom: 0;
				}
			}
		}
	}
	// Email Lists & Services
	tbody {
		tr td.wph-list--radio {
			width: 40px;
		}
		tr td.wph-list--icon {
			width: 40px;
			div {
				@extend %sprite--listings;
			}
			.wph-list {
				&--mailchimp {
					background-position: 0 0;
				}
				&--campaignmonitor {
					background-position: 0 -89px;
				}
				&--aweber {
					background-position: 0 -170px;
				}
				&--constantcontract {
					background-position: 0 -260px;
				}
				&--getresponse {
					background-position: 0 -341px;
				}
				&--sendy {
					background-position: 0 -420px;
				}
				&--madmimi {
					background-position: 0 -510px;
				}
				&--infusionsoft {
					background-position: 0 -600px;
				}
			}
			@media (max-width: 480px) {
				display: none;
			}
		}
		tr {
			td.wph-list--edit {
				width: 55px;
				.wph-button {
					opacity: 0;
					visibility: hidden;
					@media (max-width: 960px) {
						opacity: 1;
						visibility: visible;
					}
					@media (max-width: 480px) {
						margin-top: 15px;
					}
				}
				@media (max-width: 480px) {
					width: 100%;
				}
			}
			&:hover td.wph-list--edit {
				.wph-button {
					opacity: 1;
					visibility: visible;
				}
			}
			@media (max-width: 480px) {
				display: block;
				margin-bottom: 15px;
				padding-bottom: 15px;
				border-bottom: 1px solid $color--lilac;
			}
		}
	}
	// e-Newsletter Integration
	&.wph-settings--enewsletter {
		thead {
			tr th.wph-enews--setup,
			tr th.wph-enews--sync {
				text-align: center;
			}
			@media (max-width: 480px) {
				display: none;
			}
		}
		tbody {
			tr {
				&:last-child {
					@media (max-width: 480px) {
						margin-bottom: 0;
						padding-bottom: 0;
						border-bottom: 0;
					}
					td {
						padding-bottom: 0;
					}
				}
				&.wph-enews--editable {
					position: relative;
					@media (max-width: 480px) {
						padding-bottom: 64px;
					}
					&:hover {
						.wph-button {
							opacity: 1;
							visibility: visible;
						}
					}
					&:last-child {
						td.wph-enews--edit {
							@media (max-width: 480px) {
								bottom: 0;
							}
						}
						@media (max-width: 480px) {
							padding-bottom: 49px;
						}
					}
				}
			}
			tr td.wph-enews--edit {
				width: 70px;
				padding-right: 15px;
				.wph-button {
					opacity: 0;
					visibility: hidden;
					@media (max-width: 960px) {
						opacity: 1;
						visibility: visible;
					}
				}
				@media (max-width: 480px) {
					width: 100%;
					position: absolute;
					right: 0;
					bottom: 15px;
					left: 0;
					padding-right: 0;
				}
			}
			tr td.wph-enews--list,
			tr td.wph-enews--sync {
				&:before {
					@media (max-width: 480px) {
						content: attr(data-title) ":";
						display: inline-block;
						margin-right: 5px;
						color: $color--shaft;
						font-family: $font--alt;
						font-weight: 700;
						text-transform: uppercase;
					}
				}
				&:last-child {
					@media (max-width: 480px) {
						margin-bottom: 0;
					}
				}
				@media (max-width: 480px) {
					margin: 0 0 15px;
				}
			}
			tr td.wph-enews--setup,
			tr td.wph-enews--sync {
				width: 55px;
				@media (max-width: 480px) {
					width: 100%;
				}
			}
			tr td.wph-enews--sync {
				&:before {
					@media (max-width: 480px) {
						position: relative;
						top: 2px;
						line-height: 23px;
					}
				}
			}
		}
	}
	&.wph-enews--setup {
		tbody {
			tr {
				@media (max-width: 480px) {
					padding-bottom: 0;
					border-bottom: 0;
				}
			}
			tr:not(:first-child) td {
				border-top: 0;
			}
			tr td {
				padding-bottom: 0;
			}
			tr:last-child td {
				padding-bottom: 15px;
			}
		}
		tfoot {
			tr td.wph-enews--cancelsetup {
				.wph-button {
					margin: 0 auto 0 0;
				}
				@media (max-width: 480px) {
					width: 100%;
					display: block;
				}
			}
			tr td.wph-enews--savesetup {
				.wph-button {
					width: 150px;
					@media (max-width: 480px) {
						width: 100%;
					}
				}
				@media (max-width: 480px) {
					width: 100%;
					display: block;
				}
			}
		}
	}
	// Don't show modules to...
	&.wph-settings--module {
		tbody tr {
			&:last-child {
				@media (max-width: 480px) {
					margin-bottom: 0;
					padding-bottom: 0;
					border-bottom: 0;
				}
			}
		}
	}
	thead {
		tr th.wph-module--loginUser,
		tr th.wph-module--admin {
			text-align: center;
		}
	}
	tbody {
		tr td.wph-module--name,
		tr td.wph-module--loginUser,
		tr td.wph-module--admin {
			&:last-child {
				@media (max-width: 480px) {
					margin-bottom: 0;
				}
			}
			@media (max-width: 480px) {
				margin: 0 0 15px;
			}
		}
		tr td.wph-module--loginUser,
		tr td.wph-module--admin {
			&:before {
				@media (max-width: 480px) {
					content: attr(data-title) ":";
					position: relative;
					display: inline-block;
					top: 2px;
					margin-right: 5px;
					color: $color--shaft;
					line-height: 23px;
					font-family: $font--alt;
					font-weight: 700;
					text-transform: uppercase;
				}
			}
		}
		tr td.wph-module--loginUser {
			width: 130px;
			@media (max-width: 480px) {
				width: 100%;
				display: block;
			}
		}
		tr td.wph-module--admin {
			width: 70px;
			@media (max-width: 480px) {
				width: 100%;
				display: block;
			}
		}
	}
	// Table for Listings
	&.wph-table--listings {
		thead {
			background: $color--alabaster;
			tr th {
				padding: 0 10px;
				background: $color--alabaster;
				border-bottom: 1px solid $color--concrete;
				color: $color--dark;
				font: 400 12px/30px $font;
				letter-spacing: -0.17px;
				text-transform: none;
				&.wph-module--views,
				&.wph-module--conversions,
				&.wph-module--rates,
				&.wph-module--admin,
				&.wph-module--active {
					text-align: center;
				}
				&:first-child {
					padding-left: 30px;
				}
				&:last-child {
					padding-right: 30px;
				}
			}
			@media (max-width: 780px) {
				display: none;
			}
		}
		tbody {
			tr {
				@media (max-width: 780px) {
					display: block;
					border-bottom: 1px solid $color--lilac;
				}
				@media (max-width: 480px) {
					margin: 0;
				}
			}
			tr th {
				padding: 0 10px 0 30px;
				border-bottom: 1px solid $color--concrete;
				font: 700 12px/66px $font--alt;
				letter-spacing: -0.17px;
				transition: 0.2s ease;
				&.wph-module--name {
					width: 180px;
					@media (max-width: 1220px) {
						width: 160px;
					}
					@media (max-width: 780px) {
						width: 100%;
					}
				}
				@media (max-width: 780px) {
					display: block;
					padding: 15px 30px 0;
					border: 0;
					line-height: 26px;
				}
			}
			tr td {
				position: relative;
				padding: 0 10px;
				border-bottom: 1px solid $color--concrete;
				color: $color--shuttle;
				font: 400 12px/14px $font;
				transform-style: preserve-3d;
				transition: 0.2s ease;
				&.wph-module--display {
					@media (max-width: 780px) {
						padding: 0 30px 15px
					}
				}
				&.wph-module--views,
				&.wph-module--conversions,
				&.wph-module--rates {
					width: 140px;
					text-align: center;
					&:before {
						@media (max-width: 780px) {
							content: attr(data-title) ":";
							margin-right: 4px;
							font-weight: bold;
						}
					}
					@media (max-width: 1220px) {
						width: 110px;
					}
					@media (max-width: 780px) {
						width: 100%;
						text-align: left;
					}
				}
				&.wph-module--admin,
				&.wph-module--active {
					text-align: center;
					&:before {
						@media (max-width: 780px) {
							content: attr(data-title) ":";
							margin-right: 4px;
							font-weight: bold;
						}
						@media (max-width: 480px) {
							color: $color--shuttle;
							font: 700 12px/14px $font;
							text-transform: none;
						}
					}
					@media (max-width: 780px) {
						text-align: left;
					}
					@media (max-width: 480px) {
						margin: 0;
					}
				}
				&.wph-module--admin {
					width: 124px;
					@media (max-width: 780px) {
						width: 100%;
					}
				}
				&.wph-module--active {
					width: 144px;
					@media (max-width: 780px) {
						width: 100%;
					}
				}
				.wph-module--edit {
					opacity: 0;
					position: absolute;
					top: 50%;
					left: -55px;
					transform: translate(0,-50%);
					transition: opacity 0.2s linear, left 0.3s ease-out;
					background: $color--white;
					box-shadow: 0 0 20px 15px rgba(255,255,255,.8);
					&:hover {
						background: $color--jumbo;
						color: $color--white;
					}
					@media (max-width: 780px) {
						display: none;
					}
				}
				span.toggle {
					@media (max-width: 780px) {
						display: inline-block;
					}
				}
				&:last-child {
					padding-right: 30px;
					@media (max-width: 780px) {
						padding: 0 30px 20px;
					}
					@media (max-width: 480px) {
						padding: 0 30px 5px;
					}
				}
				@media (max-width: 1220px) {
					padding: 15px 10px;
				}
				@media (max-width: 780px) {
					display: block;
					padding: 0 30px 15px;
					border: 0;
				}
			}
			tr:hover th,
			tr:hover td {
				background: $color--white;
			}
			tr:hover td .wph-module--edit {
				opacity: 1;
				left: 10px;
			}
			tr:last-child {
				th, td {
					border-bottom: 0;
				}
				@media (max-width: 780px) {
					border-bottom: 0;
				}
			}
			@media (max-width: 780px) {
				display: block;
			}
		}
		@media (max-width: 780px) {
			display: block;
		}
	}
	// Global Responsive for .wph-table
	@media (max-width: 480px) {
		width: 100%;
		display: block;
	}
}