.hustle-two :focus,
.hustle-two textarea:focus,
.hustle-two select:focus,
.hustle-two input:focus {
	outline-color: transparent;
	outline-style: none;
	box-shadow: none;
}
.hustle-two .input,
.hustle-two input[type="text"],
.hustle-two input[type="email"],
.hustle-two input[type="search"],
.hustle-two input[type="password"],
.hustle-two select,
.hustle-two textarea {
	color: #999;
	display: block;
	font: 500 15px/25px 'Roboto';
	width: 100%;
	max-width: 100%;
	padding: 7px 15px 6px;
	margin: 0px 0px 15px 0px;
	border: 1px solid #DDD;
	background: #FFF;
	border-radius: 4px;
	box-shadow: none;
	transition-property: background, color, border-color;
	transition-duration: 0.3s;
}
.hustle-two .input:hover,
.hustle-two .input:focus,
.hustle-two input[type="text"]:hover,
.hustle-two input[type="text"]:focus,
.hustle-two input[type="email"]:hover,
.hustle-two input[type="email"]:focus,
.hustle-two input[type="search"]:hover,
.hustle-two input[type="search"]:focus,
.hustle-two input[type="password"]:hover,
.hustle-two input[type="password"]:focus,
.hustle-two select:hover,
.hustle-two select:focus,
.hustle-two textarea:hover,
.hustle-two textarea:focus {
	border-color: #BBB;
	color: #3D464D;
}
.hustle-two .input:hover:not(:focus),
.hustle-two input[type="text"]:hover:not(:focus),
.hustle-two input[type="email"]:hover:not(:focus),
.hustle-two input[type="search"]:hover:not(:focus),
.hustle-two input[type="password"]:hover:not(:focus),
.hustle-two select:hover:not(:focus),
.hustle-two textarea:hover:not(:focus) {
	color: #3D464D;
}
.hustle-two .input:disabled,
.hustle-two .input.disabled,
.hustle-two input[type="text"]:disabled,
.hustle-two input[type="text"].disabled,
.hustle-two input[type="email"]:disabled,
.hustle-two input[type="email"].disabled,
.hustle-two input[type="search"]:disabled,
.hustle-two input[type="search"].disabled,
.hustle-two input[type="password"]:disabled,
.hustle-two input[type="password"].disabled,
.hustle-two select:disabled,
.hustle-two select.disabled,
.hustle-two textarea:disabled,
.hustle-two textarea.disabled {
	background: #F6F6F6 !important;
	color: #AAA !important;
	border-color: #DDD !important;
	cursor: default;
}
.hustle-two input[type="text"]::-webkit-input-placeholder,
.hustle-two input[type="email"]::-webkit-input-placeholder,
.hustle-two input[type="search"]::-webkit-input-placeholder,
.hustle-two input[type="password"]::-webkit-input-placeholder,
.hustle-two textarea::-webkit-input-placeholder {
	position: relative;
	top: 0;
}
.hustle-two input[type="text"]:focus::-webkit-input-placeholder,
.hustle-two input[type="email"]:focus::-webkit-input-placeholder,
.hustle-two input[type="search"]:focus::-webkit-input-placeholder,
.hustle-two input[type="password"]:focus::-webkit-input-placeholder,
.hustle-two textarea:focus::-webkit-input-placeholder {
	transition: opacity 0.3s 0.4s ease;
	opacity: 0.25;
}

