// Input: Checkbox
.hustle-two {
	.wph-input--checkbox {
		display: inline-block;
		margin: 0 10px 0 0;
		&_block {
			display: block;
			margin: 0 auto;
		}
	}
	.wph-input--checkbox,
	.wph-input--checkbox_block {
		width: 20px;
		height: 20px;
		input, input[type="checkbox"] {
			display: none !important;
		}
		input[type="checkbox"] {
			& + label {
				width: 20px;
				height: 20px;
				cursor: pointer;
				display: block;
				margin: 0;
				border-radius: 3px;
				background: $color--pale;
			}
			& +	label:before {
				display: block;
				padding: 1px 0 0;
				border-radius: 3px;
				color: $color--pale;
				font-size: 11px;
				line-height: 20px;
				text-align: center;
			}
			&:checked + label {
				background: $color--blue;
			}
			&:checked + label:before {
				color: $color--white;
			}
		}
	}
}

// Input: Radio
.hustle-two {
	.wph-input--radio {
		display: inline-block;
		margin: 0 10px 0 0;
		&_block {
			display: block;
			margin: 0 auto;
		}
	}
	.wph-input--radio,
	.wph-input--radio_block {
		width: 20px;
		height: 20px;
		input, input[type="radio"] {
			display: none !important;
		}
		input[type="radio"] {
			& + label {
				width: 20px;
				height: 20px;
				cursor: pointer;
				display: block;
				margin: 0;
				border-radius: 100%;
				background: $color--pale;
			}
			& +	label:before {
				display: block;
				padding: 1px 0 0;
				border-radius: 100%;
				color: $color--pale;
				font-size: 11px;
				line-height: 20px;
				text-align: center;
			}
			&:checked + label {
				background: $color--blue;
			}
			&:checked + label:before {
				color: $color--white;
			}
		}
	}
}

.hustle-two {
	// Input: Text
	input[type="text"],
	textarea {
		border: 1px solid $color--gainsboro;
		border-radius: 3px;
		background: $color--white;
		color: $color--montana;
		font-weight: 400;
		font-size: 15px;
		&:last-child {
			margin-bottom: 0;
		}
		&:-moz-placeholder {
			color: $color--sand;
		}
		&::-moz-placeholder {
			color: $color--sand;
		}
		&:-ms-input-placeholder {
			color: $color--sand;
		}
		&::-webkit-input-placeholder {
			color: $color--sand;
		}
	}
	// Textarea
	textarea {
		height: 210px;
		resize: none;
	}
	// Input: Number
	.wph-input--number {
		width: 70px;
		position: relative;
		&.wph-input--number_100{
			width: 100px;
		}
		&:hover {
			.wph-nbr--nav {
				opacity: 1;
				visibility: visible;
			}
		}
		input[type=number]::-webkit-inner-spin-button,
		input[type=number]::-webkit-outer-spin-button {
			display: none;
			margin: 0;
			-webkit-appearance: none;
		}
		input {
			width: 100%;
			height: 40px;
			display: block;
			margin: 0;
			padding: 0 20px 0 15px;
			border: 1px solid $color--gainsboro;
			border-radius: 3px;
			color: $color--montana;
			font: 400 15px/18px $font;
			-webkit-appearance: textfield;
			-moz-appearance: textfield;
			&:focus {
				outline: 0;
			}
			&:hover, &:focus, &:active {
				+ .wph-nbr--nav {
					opacity: 1;
					visibility: visible;
				}
			}
		}
		.wph-nbr--nav {
			width: 16px;
			height: 40px;
			opacity: 0;
			visibility: hidden;
			position: absolute;
			top: 0;
			right: 0;
			border: 1px solid $color--gainsboro;
			border-radius: 0 3px 3px 0;
			background: $color--flash;
			transition: 0.2s ease-in;
		}
		.wph-nbr--button {
			width: 100%;
			cursor: pointer;
			user-select: none;
			position: relative;
			color: $color--montana;
			font-size: 13px;
			line-height: 1.5;
			font-family: "Trebuchet MS", Helvetica, sans-serif;
			text-align: center;
			&.wph-nbr--up {
				height: 50%;
				position: absolute;
				top: 0;
			}
			&.wph-nbr--down {
				height: 50%;
				position: absolute;
				bottom: 0;
			}
		}
	}
	// Select
	.select-container {
		min-width: 150px;
	}
	// Input: Tabs
	.wph-input--tabs {
		label + input {
			display: none;
		}
	}
}