.hustle-two .wpoi-conditions-wrap {
	
	display: flex;
	justify-content: space-between;
	flex-direction: row;
	
	.wpoi-conditions-block {
		flex: 1;
	}
	
	.wpoi-conditions-available {
		
		max-width: 248px;
		margin-right: 30px;
		
		.wpoi-conditions-list {
			
			height: 250px;
			overflow-y: auto;
			padding: 10px;
			border: 1px solid #E5E5E5;
			
			.wpoi-condition-item {
				
				position: relative;
				margin-bottom: 10px;
				padding: 4px 30px 4px 10px;
				border: 1px solid #979797;
				border-color: rgba(151,151,151,0.4);
				border-radius: 2px;
				cursor: pointer;
				color: #777777;
				font: 400 13px/20px "Open Sans", sans-serif;
				text-align: left;
				transition: 0.3s ease;
				
				&:last-child {
					margin-bottom: 0;
				}
				
				&:hover {
					
					border-color: #979797;
					
					.dashicons-before:before {
						opacity: 1;
					}
					
				}
				
				&.disabled {
					
					opacity: 0.5;
					cursor: default;
					
					&:hover {
						border-color: rgba(151,151,151,0.4);
					}
					
					.dashicons-before:before {
						cursor: default;
					}
					
					.dashicons-before:before,
					.dashicons-before:hover:before {
						opacity: 0.5;
					}
					
				}
				
				&.added {
					
					background: #B0B0B0;
					background-color: rgba(176,176,176,0.5);
					border-color: transparent;
					color: #FFF;
					
					&:hover {
						background-color: #979797;
					}
					
				}
				
				.dashicons-before {
					
					height: 20px;
					position: absolute;
					right: 10px;
					top: 4px;
					cursor: pointer;
					line-height: 20px;
					
					&:before {
						opacity: 0.6;
					}
					
					&:hover:before {
						opacity: 1;
					}
					
				}
				
				.wpoi-add:before {
					content: "\f502";
				}
				
				.wpoi-remove:before {
					content: "\f147";
					opacity: 1;
					color: #FFF;
				}
				
			}
			
		}
		
	}
	
	.wpoi-conditions-met {
		
		.wpoi-conditions-list {
			
			height: 250px;
			overflow-y: auto;
			padding: 10px;
			border: 1px solid #E5E5E5;
			
		}
		
		.wpoi-condition-item {
			
			border: 1px solid #DDD;
			margin-bottom: 10px;
			
			&:last-child {
				margin-bottom: 0;
			}
			
			&.closed {
				
				header {
					border-bottom: 0;
				}
				
				section {
					display: none !important;
				}
				
			}
			
			header {
				
				margin-bottom: 0;
				padding: 5px 10px;
				border-bottom: 1px solid #DDD;
				border-radius: 0;
				background: #F2F4F5;
				
				h6 {
					position: relative;
					margin-bottom: 0;
					color: #848484;
					font: 600 13px/20px "Open Sans", sans-serif;
					text-transform: none;
					text-align: left;
					
					.dashicons-before {
						position: absolute;
						right: 0;
						cursor: pointer;
						color: #777771;
					}
					
					.wpoi-arrow-up:before {
						content: "\f142";
					}
					
					.wpoi-arrow-down:before {
						content: "\f140";
					}
					
				}
				
			}
			
			section {
				
				position: relative;
				padding: 10px;
				
				label {
					margin-bottom: 5px;
					padding: 0;
					color: #000;
					font: 400 12px/20px "Open Sans", sans-serif;
					text-align: left;
				}
				
				.select-container {
					max-width: 274px;
				}
				
				button {
					
					width: 20px;
					height: 20px;
					position: absolute;
					right: 10px;
					bottom: 10px;
					display: block;
					padding: 0;
					border-radius: 0;
					background: transparent;
					background-color: transparent;
					box-shadow: none;
					color: #D0021B;
					
					&:hover, &:focus, &:active {
						background: transparent;
						background-color: transparent;
						box-shadow: none;
					}
					
					&[tooltip]:after {
						width: 100px;
					}
					
					span {
						width: 20px;
						height: 20px;
						display: block;
					}
					
					.wpoi-trash:before {
						content: "\f182";
					}
					
				}
				
				textarea {
					height: 100px;
					resize: none;
					margin-bottom: 0;
					padding: 10px;
					border-radius: 0;
					font-size: 13px;
					font-weight: 400;
				}
				
				.rule-form > em {
					display: block;
					margin-top: 10px;
					padding: 1px 10px 0;
					background: #F5A623;
					color: #FFF;
					font: 400 13px/20px "Roboto Condensed", "Roboto", sans-serif;
					font-style: normal;
				}
				
				.select2-container {
					width: 100% !important;
					display: block !important;
				}
				
			}
			
		}
		
	}
	
}

