.hustle-two .wph-conditions {
	width: 100%;
	display: flex;
	justify-content: space-between;
	flex-direction: row;
	.wph-conditions--side {
		flex: 0 210px;
		.wph-conditions--items {
			height: 300px;
			overflow-y: scroll;
			padding: 5px 20px 5px 5px;
			border: 1px solid $color--athens;
			border-right: 0;
			border-radius: 3px 0 0 3px;
			background: $color--alabaster;
			.wph-conditions--item {
				cursor: pointer;
				margin-bottom: 5px;
				padding: 5px;
				border: 1px solid $color--silver;
				border-radius: 2px;
				background: $color--white;
				color: $color--gray;
				font: 400 12px/16px $font;
				&.added, &:hover {
					border-color: $color--silver;
					background: $color--silver;
					color: $color--white;
				}
				&.disabled {
					opacity: 0.5;
					cursor: default;
					pointer-events: none;
				}
				&:last-child {
					margin-bottom: 0;
				}
			}
		}
	}
	.wph-conditions--box {
		flex: 1;
		.wph-conditions--items {
			height: 300px;
			overflow-y: scroll;
			padding: 5px;
			border: 1px solid $color--athens;
			border-radius: 0 3px 3px 0;
			background: $color--white;
			transform-style: preserve-3d;
			.wph-conditions--item {
				margin-bottom: 5px;
				&:last-child {
					margin-bottom: 0;
				}
			}
			.wph-conditions--item header {
				padding: 0 10px;
				border: 1px solid $color--silver;
				border-radius: 2px 2px 0 0;
				background: $color--haze;
				box-shadow: 0 1px 0 rgba(0,0,0,0.03);
				.wph-condition--name,
				.wph-condition--preview {
					display: inline-block;
					margin: 0;
					padding: 0;
					font: 500 12px/30px $font;
				}
				.wph-condition--preview {
					opacity: 0.8;
					margin-left: 20px;
					font-weight: 400;
					font-style: italic;
					.wph-icon {
						opacity: 0.3;
						margin-right: 5px;
					}
				}
			}
			.wph-conditions--item section {
				padding: 15px;
				border: 1px solid $color--silver;
				border-top: 0;
				border-radius: 0 0 2px 2px;
				background: $color--white;
				box-shadow: 0 1px 0 rgba(0,0,0,0.03);
				.wph-label--legend {
					margin: 0;
				}
				.rule-description label {
					margin: 0 0 15px;
				}
			}
			.wph-conditions--item.wph-conditions--closed section {
				display: none;
			}
		}
		.wph-conditions--empty {
			position: relative;
			top: 50%;
			transform: translateY(-50%);
			padding: 0 40px;
			color: $color--gray;
			font: 500 15px/20px $font;
			text-align: center;
		}
	}
}