.hustle-two {
	p {
		color: $color--montana;
		font: 400 12px/20px $font;
		strong {
			font-weight: 700;
		}
	}
	.wph-p {
		&--info {
			color: $color--jumbo;
			font: 400 14px/30px $font--alt;
			letter-spacing: -0.15px;
			&:before {
				content: "";
				width: 18px;
				height: 16px;
				display: inline-block;
				position: relative;
				top: 1px;
				margin-right: 10px;
				margin-right: 10px;
				@extend %sprite--icons;
				background-position: -183px -53px;
			}
		}
	}
	.wph-tag {
		&--soon,
		&--premium {
			display: inline-block;
			position: relative;
			top: -2px;
			margin-left: 10px;
			padding: 1px 15px 0;
			border-radius: 24px;
			color: $color--shaft;
			font: 400 12px/22px $font;
			letter-spacing: -0.3px;
			text-transform: uppercase;
			text-align: center;
		}
		&--soon {
			background: $color--corn;
		}
		&--premium {
			background: $color--patblue;
		}
	}
	.wph-shortcode {
		padding: 10px;
		border: 1px solid $color--gainsboro;
		border-radius: 3px;
		background: $color--concrete;
		color: $color--montana;
		font: 400 15px/18px $font;
	}
}