.hustle-two .wph-triggers {
	.wph-triggers--tabs {
		width: auto;
		display: block;
		margin: 30px 0;
		li {
			min-width: 140px;
			display: inline-block;
			margin: 0;
			padding: 0 15px;
			border: 1px solid $border--crystal;
			border-left: 0;
			background: $color--haze;
			&:first-child {
				border-left: 1px solid $border--crystal;
				border-radius: 10px 0 0 10px;
			}
			&:not(:first-child) {
				margin-left: -4px;
			}
			&:last-child {
				border-radius: 0 10px 10px 0;
			}
			label {
				cursor: pointer;
				display: block;
				margin: 0 !important;
				padding: 7px 5px;
				border-radius: inherit;
				color: $color--tangaroa;
				font: 600 11px/14px "Open Sans", Arial, sans-serif;
				text-align: center;
			}
			&:hover,
			&.current {
				border-color: $color--manatee;
				background: $color--manatee;
				label {
					color: $color--white;
				}
			}
		}
		input {
			display: none;
		}
		&.wph-triggers--nomargin {
			margin: 0;
		}
		&.wph-triggers--xsmargin {
			margin: 0 0 15px;
		}
	}
	.wph-triggers--content {
		.wph-triggers--option {
			display: none;
			&.current {
				display: block;
			}
		}
	}
}

.hustle-two.hustle-two .wph-toggletabs {
	border-bottom: 1px solid $color--lilac;
	&:last-child,
	&:last-of-type {
		border-bottom: 0;
	}
	&.wph-toggletabs--open {
		padding: 0 30px 30px;
		.wph-toggletabs--title {
			padding: 0;
			border-bottom: 1px solid $color--lilac;
			&.can-open .open {
				top: 30px;
				right: 0;
			}
			.i-arrow {
				transform: rotate(180deg);
			}
		}
		.wph-toggletabs--content,
		.wph-toggletabs--footer {
			display: block;
		}
	}
	.wph-toggletabs--title {
		position: relative;
		padding: 0 30px;
		&, & h4 {
			text-align: left;
		}
		&.can-open .open {
			top: 29px;
			right: 30px;
			.wph-icon {
				display: block;
				vertical-align: middle;
				font-size: 14px;
			}
		}
	}
	.wph-toggletabs--content,
	.wph-toggletabs--footer {
		display: none;
	}
	.wph-toggletabs--content {
		padding: 40px 0 0;
	}
	.wph-toggletabs--footer {
		margin-top: 30px;
		padding: 30px 0 0;
		border-top: 1px solid $color--lilac;
		.wph-button {
			width: auto;
			display: inline-block;
		}
		.col-half:last-child {
			text-align: right;
			.wph-button:not(:last-child) {
				margin-right: 10px;
			}
			.wph-button-save span.spinner-container {
				position: relative;
			}
			.wph-button-save span.button-spinner {
				right: -5px;
				top: 0;
			}
		}
	}
}