		flex-basis: 83.33333333%;
	}
	.col-xs-11 {
		max-width: 91.66666667%;
		flex-basis: 91.66666667%;
	}
	.col-xs-12 {
		max-width: 100%;
		flex-basis: 100%;
	}
	.col-xs-hidden {
		@media (max-width: 767px) {
			display: none;
		}
