.hustle-two {
	
	img.wpoi-superhero {
		width: 100%;
		max-width: 162px;
		height: auto;
		display: block;
	}
	
	.wpoi-float-left-image-bottom img {
		margin-bottom: -30px;
	}
	
}

// ============================================== //
// ================= HUSTLE 2.0 ================= //
// ============================================== //
// ======== by Leighton Sapir (WPMU DEV) ======== //
// ============================================== //

.hustle-two {
	.wph-img {
		&--soon {
			width: 130px;
			height: auto;
			display: block;
			margin: 0 auto 15px;
		}
		&--character {
			width: 100%;
			max-width: 162px;
			height: auto;
			display: block;
			position: relative;
		}
	}
	.wph-media {
		&--holder {
			width: 210px;
			height: 210px;
			position: relative;
			margin-bottom: 10px;
			border-radius: 3px;
			background-color: #F2F4F5;
			background-image: linear-gradient(45deg, white 25%, transparent 25%, transparent 75%, white 75%, white), linear-gradient(45deg, white 25%, transparent 25%, transparent 75%, white 75%, white);
			background-size: 30px 30px;
			background-position: 0 0, 15px 15px;
		}
		&--preview {
			width: 100%;
			height: 100%;
			position: absolute;
			z-index: 1;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			border-radius: 3px;
			background-repeat: no-repeat;
			background-size: contain;
			background-position: center;
			box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.1);
		}
		&--options {
			z-index: 9;
			* {
				z-index: 99;
			}
			.wph-button--dots {
				position: absolute;
				right: 10px;
			}
			.wph-button--dots {
				top: 10px;
			}
			&.hidden{
				display: none;
			}
		}
		&--list {
			min-width: 210px;
			position: absolute;
			top: 40px;
			.svg-triangle {
				position: absolute;
				top: -9px;
				right: 20px;
				z-index: 999;
				svg {
					width: 10px;
					height: 9px;
					margin: 0 auto;
					vertical-align: super;
				}
				polygon {
					fill: $color--white;
					stroke: $color--lilac;
					stroke-width: 1;
				}
			}
		}
		&--items {
			display: block;
			position: relative;
			margin: 0;
			padding: 20px 30px;
			border: 1px solid $color--lilac;
			border-radius: 5px;
			background: $color--white;
			box-shadow: 0 0 5px 0 rgba(0,0,0,0.10);
			li {
				margin: 0;
				padding: 14px 0;
				border-bottom: 1px solid $color--lilac;
				&:last-child {
					padding-bottom: 0;
					border-bottom: 0;
				}
			}
			li, li a {
				color: #6D6D6D;
				font: 300 15px/20px $font;
				&:hover {
					color: $blue;
				}
				.dashicons {
					margin-right: 4px;
				}
			}
		}
		&--title {
			position: relative;
			span {
				display: block;
				padding-bottom: 14px;
				border-bottom: 1px solid #EEE;
				color: #77776F;
				font: bold 15px/20px "Roboto Condensed", "Roboto", sans-serif;
			}
			i.dev-icon {
				height: 20px;
				position: absolute;
				z-index: 999;
				top: 0;
				right: 0;
				font-size: 16px;
				line-height: 18px;
				cursor: pointer;
				transition: color 0.3s ease-in-out;
				&:hover {
					color: #14B4D1;
				}
			}
		}
	}
}