.hustle-two {
	.wph-howdy,
	.wph-okay {
		width: 130px;
		height: 130px;
		display: block;
		margin: 30px auto 0;
		background: url('../img/wph-sprite.png') no-repeat $color--white;
		background-image: url('../img/wph-sprite.svg'), none;
		background-position: -892px 0;
		@media (min-width: 48em) {
			width: 100%;
			max-width: 226px;
			height: 239px;
			margin: 0 auto;
		}
	}
	.wph-howdy {
		@media (min-width: 48em) {
			background-position: -388px 19px;
		}
		@media (min-width: 75em) {
			background-position: -371px 19px;
		}
	}
	.wph-okay {
		@media (min-width: 48em) {
			background-position: -653px 19px;
		}
		@media (min-width: 75em) {
			background-position: -628px 19px;
		}
	}
}

.hustle-two {
	#wph-woop {
		.box-content {
			.flex-row .col-lg-9 {
				.content-800 {
					margin: 10px auto 30px;
					text-align: center;
					@media (min-width: 48em) {
						margin: 0 0 0 7px;
						text-align: left;
					}
					@media (min-width: 48em) {
						position: relative;
						top: 50%;
						transform: translateY(-50%);
					}
				}
				@media (min-width: 48em) {
					height: 239px;
					transform-style: preserve-3d;
				}
			}
		}
	}
}

#wph-dashboard {
	// Widget: Welcome
	#wph-welcome {
		.box-content {
			.flex-row .col-lg-9 {
				.content-800 {
					margin: 10px auto 30px;
					text-align: center;
					@media (min-width: 48em) {
						margin: 0 0 0 7px;
						text-align: left;
					}
					@media (min-width: 48em) {
						position: relative;
						top: 50%;
						transform: translateY(-50%);
					}
				}
				@media (min-width: 48em) {
					height: 239px;
					transform-style: preserve-3d;
				}
			}
		}
	}
	// Widget: Welcome [On] & [Off]
	#wph-welcome-on,
	#wph-welcome-off {
		.box-content {
			.flex-row .col-lg-4 {
				.content-322 {
					margin: 10px auto 20px;
					text-align: center;
					@media (min-width: 64em) {
						position: relative;
						top: 50%;
						margin: 0 auto;
						transform: translateY(-50%);
					}
				}
				@media (min-width: 64em) {
					height: 239px;
					transform-style: preserve-3d;
				}
			}
			.flex-row .col-lg-5 {
				.content-374 {
					max-width: 100%;
					margin: 10px auto 30px 0;
					@media (min-width: 48em) {
						position: relative;
						top: 50%;
						transform: translateY(-50%);
					}
					@media (min-width: 64em) {
						max-width: 374px;
						margin: 0 0 0 auto;
					}
				}
				@media (min-width: 48em) {
					height: 239px;
					transform-style: preserve-3d;
				}
			}
		}
	}
	// Widget: Stats
	#wph-module-stats {
		.wph-module--graph {
			margin: 0 0 30px;
			canvas {
				margin: 0 auto;
			}
			@media (min-width: 64em) {
				max-width: 527px;
				margin: 30px auto;
			}
		}
		.wph-module--stats {
			margin: 30px 0 40px;
			thead tr th,
			tbody tr td {
				border-right: 1px solid $color--lilac;
				&:last-child {
					border-right: 0;
				}
			}
			thead tr {
				th {
					border-right-color: transparent;
					border-bottom: 1px solid $color--lilac;
					line-height: 28px;
				}
				th:not(.wph-stats--name) {
					text-align: center;
				}
			}
			tbody tr {
				&:first-child {
					td {
						border-top: 0;
					}
				}
				td {
					padding: 18px 0;
				}
				td:not(.wph-stats--name) {
					width: 80px;
					text-align: center;
					&:last-child {
						color: $color--shaft;
						font-weight: 700;
					}
				}
				td i:not(.wph-icon) {
					width: 6px;
					height: 6px;
					display: inline-block;
					position: relative;
					top: -2px;
					margin-right: 10px;
					border-radius: 100%;
					&.wph-stats--yellow {
						background: $color--yellow;
					}
					&.wph-stats--green {
						background: $color--green;
					}
					&.wph-stats--blue {
						background: $color--maya;
					}
				}
				td span.wpoi-tooltip {
					width: 22px;
					display: inline-block;
				}
				td [tooltip]:after {
					width: auto;
					padding: 5px 12px 4px;
				}
			}
			@media (min-width: 64em) {
				max-width: 498px;
				margin: 31px 0 30px 4px;
			}
		}
	}
	// Widget: Setup
	#wpoi-box-dashboard-optins {
		.box-content h6 {
			margin: 30px 0 10px;
			letter-spacing: 0;
			text-align: center;
		}
		.wph-dashboard--steps {
			margin-bottom: 47px;
			tbody tr {
				td {
					padding: 20px 0 10px;
					.wph-table--title,
					.wph-table--subtitle {
						max-width: 323px;
						letter-spacing: 0;
					}
				}
				&:first-child td {
					border-top: 0;
				}
				td.wph-dashstep--icon {
					width: 74px;
					div {
						@extend %sprite--dashboard;
						height: 64px;
						margin-right: 10px;
						margin-left: 6px;
					}
					.wph-dashstep {
						&--optins {
							background-position: 0 12px;
						}
						&--ccontent {
							background-position: 3px -73px;
						}
						&--ssharing {
							background-position: 0 -159px;
						}
						&--srewards {
							background-position: 2px -248px;
						}
					}
					@media (max-width: 480px) {
						display: none;
					}
				}
				td.wph-dashstep--create {
					width: 70px;
				}
				&:last-child td {
					border-bottom: 1px solid $color--lilac;
				}
			}
		}
	}
	// Widget: Edit
	#wph-modules--overview {
		.wph-triggers {
			margin-top: 30px;
			margin-bottom: 30px;
		}
		.wph-table {
			&:first-child {
				margin-bottom: 21px;
			}
			&:last-child {
				margin-bottom: 30px;
			}
		}
		.wph-table thead tr {
			th {
				line-height: 30px;
			}
		}
		.wph-table tbody tr {
			td {
				padding-bottom: 12px;
			}
			.wph-module--edit {
				width: 55px;
				.wph-button {
					opacity: 0;
					visibility: hidden;
				}
			}
			&:hover {
				.wph-module--edit .wph-button {
					opacity: 1;
					visibility: visible;
				}
			}
		}
		.wph-modules-overview {
			display: none;
			&.current {
				display: block;
			}
		}
	}
}