.hustle-two {
	.wph-button {
		height: auto;
		cursor: pointer;
		margin: 0;
		padding: 9px 15px;
		border: 2px solid transparent;
		border-radius: 5px;
		background: transparent;
		box-shadow: none;
		font: 400 15px/18px $font;
		text-transform: uppercase;
		text-align: center;
		text-decoration: none;
		transition: 0.2s ease-in;
		&.wph-button--small {
			padding: 0 15px;
			border-radius: 3px;
			font: 400 12px/26px $font--alt;
			.button-spinner{
				right:5px;
				top: 18%;
			}
		}
		&.wph-button--inline {
			height: 30px;
			display: inline-block;
		}
		&:disabled {
			cursor: default;
		}
		@media (max-width: 420px) {
			width: 100%;
			display: block;
			margin: 0 auto;
		}
	}
	.wph-button--gray {
		border-color: $color--jumbo;
		color: $color--jumbo;
		&.wph-button--open,
		&:hover, &:active, &:focus {
			background: $color--jumbo;
			color: $color--white;
		}
		&:disabled {
			background: #e6e6e6;
		}
	}
	.wph-button--blue {
		border-color: $color--blue;
		color: $color--blue;
		&.wph-button--open,
		&:hover, &:active, &:focus {
			background: $color--blue;
			color: $color--white;
		}
	}
	.wph-button--red {
		border-color: $color--red;
		color: $color--red;
		&.wph-button--open,
		&:hover, &:active, &:focus {
			background: $color--red;
			color: $color--white;
		}
	}
	// Filled Buttons
	.wph-button--filled {
		&.wph-button--gray {
			background: $color--jumbo;
			color: $color--white;
			&:hover, &:active, &:focus {
				background: $color--montana;
				border-color: $color--montana;
			}
			&:disabled {
				background: #e6e6e6;
				border-color: #e6e6e6;
			}
		}
		&.wph-button--blue {
			background: $color--blue;
			color: $color--white;
			&:hover, &:active, &:focus {
				background: $color--darkblue;
				border-color: $color--darkblue;
			}
		}
	}
	.wph-button--dots {
		height: auto;
		padding: 8px 4px;
		border: 1px solid $color--lilac;
		background: $color--white;
		box-shadow: none;
		line-height: 4px;
		text-align: center;
		transition: 0.3s ease-in-out;
		.dot {
			width: 4px;
			height: 4px;
			float: left;
			display: block;
			vertical-align: middle;
			margin-right: 4px;
			border-radius: 50%;
			background: $color--silver;
			transition: 0.3s ease-in-out;
			&:last-child {
				margin-right: 0;
			}
			&:hover, &:active, &:focus {
				background: $color--blue;
			}
		}
		&:hover, &:active, &:focus {
			background: $color--white;
			.dot {
				background: $color--blue;
			}
		}
	}
	// Floated Button
	.wph-button--fleft {
		display: inline-block;
		position: relative;
		top: -5px;
		margin-left: 15px;
		&.wph-button--small {
			line-height: 26px;
		}
	}
	// Media Buttons
	.wph-media--add {
		width: 100%;
		height: 100%;
		position: absolute;
		z-index: 2;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		border: 0;
		border-radius: inherit;
		box-shadow: none;
		color: $color--montana;
		font: 400 15px/18px $font;
		text-transform: none;
		&:before {
			content: "\f128";
			display: block;
			margin: 0 auto 15px;
			color: #A9B3BB;
			font-size: 40px;
			font-family: 'dashicons';
		}
		&:hover {
			background: transparent;
			color: $color--montana;
		}
	}
}