.hustle-two .toggle {
	position: relative;
	display: inline-block;
	vertical-align: middle;
	white-space: nowrap;
}
.hustle-two .toggle .toggle-checkbox {
	display: none;
}
.hustle-two .toggle .toggle-label {
	background: #1ABC9C;
	display: inline-block;
	position: relative;
	height: 21px;
	width: 34px;
	padding: 0;
	margin: 0;
	border-radius: 11px;
	transition: background .2s;
}
.hustle-two .toggle .toggle-label:before {
	content: '';
	position: absolute;
	transition: all .2s;
	border-radius: 11px;
	background: #DDD;
}
.hustle-two .toggle .toggle-label:after {
	content: '';
	background: #FCFCFC;
	display: inline-block;
	position: relative;
	width: 19px;
	height: 19px;
	transition: margin .2s;
	border-radius: 11px;
	margin: 1px;
	box-shadow: 0 0 0 1px rgba(0,0,0,0.1);
}
.hustle-two .toggle .toggle-checkbox:hover + .toggle-label:after,
.hustle-two .toggle .toggle-label:hover:after {
	background: #FFF;
	box-shadow: 0 0 15px 0px rgba(0,0,0,0.03), 0 5px 2px 0px rgba(0,0,0,0.05), 0 0 0 1px rgba(0,0,0,0.2);
}
.hustle-two .toggle .toggle-checkbox + .toggle-label {
	background: transparent;
}
.hustle-two .toggle .toggle-checkbox + .toggle-label:before {
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
}
.hustle-two .toggle .toggle-checkbox + .toggle-label:after {
	margin-left: 1px;
}
.hustle-two .toggle .toggle-checkbox:checked + .toggle-label {
	background: #1ABC9C;
}
.hustle-two .toggle .toggle-checkbox:checked + .toggle-label:before {
	top: 1px;
	left: 14px;
	right: 1px;
	bottom: 1px;
}
.hustle-two .toggle .toggle-checkbox:checked + .toggle-label:after {
	margin-left: 14px;
}
.hustle-two .toggle.disabled label,
.hustle-two .toggle.loading label {
	opacity: 0.6;
	cursor: default;
}
.hustle-two .toggle.disabled .toggle-checkbox:hover + .toggle-label:after,
.hustle-two .toggle.loading .toggle-checkbox:hover + .toggle-label:after,
.hustle-two .toggle.disabled .toggle-label:hover:after,
.hustle-two .toggle.loading .toggle-label:hover:after {
	background: #FCFCFC;
	box-shadow: 0 0 0 1px rgba(0,0,0,0.1);
}
.hustle-two .toggle.disabled:before,
.hustle-two .toggle.loading:before {
	vertical-align: middle;
	margin-right: 6px;
}

.hustle-two .toggle {
	
	cursor: pointer;
	
	input, label {
		cursor: pointer;
	}
	
}

.hustle-two .toggle-content {
	
	padding: 0;
	
	&.toggle-content-r {
		padding-left: 60px;
	}
	
	&.toggle-content-l {
		padding-right: 60px;
	}
	
}
.hustle-two .accordion-block .one-line input.toggle-checkbox{
	display: none;
}