#wph-ccontent--designtab,
.hustle-two #wph-ccontent--designtab,
.hustle-two #wph-ccontent--designtab {
	// Section: Name
	#wph-ccontent--name {}
	// Section: The Content
	#wph-ccontent--content {
		.wp-editor-tabs,
		#wp-custom_content-editor-tools,
		#wp-custom_content-editor-tools .wp-editor-tabs {
			//display: none;
		}
	}
	// Section: Colors & Design
	#wph-ccontent--design {
		.wph-flex--box:not(.wph-flex--side) {
			h4 {
				text-align: left;
			}
			.wph-label--border {
				margin-bottom: 0;
			}
		}
	}
	// Footer
	.wph-toggletabs--footer {
		margin-top: 0;
	}
}