// THESE STYLES SHOULD BE MOVED
// WHEN DOING THE CSS GARDENING

// 1. GLOBAL STYLES, TYPOGRAPHY
.hustle-two p {
	display: block;
	margin: 0.5em 0;
	padding: 0;
	font-family: 'Roboto', sans-serif;
	font-weight: 400;
	font-size: 15px;
	line-height: 29px;
	color: #777771;
}
.hustle-two p small {
	font-size: 12.5px;
	line-height: 22px;
	display: inline-block;
	color: #555550;
}
.hustle-two p big {
	font-size: 18px;
	line-height: 28px;
	display: inline-block;
	font-weight: 400;
	color: #888882;
}
.hustle-two p:first-child {
	margin-top: 0;
}
.hustle-two p:last-child {
	margin-bottom: 0;
}
.hustle-two .notice p {
	margin:5px 0;
}
.hustle-two dfn {
	font-style: normal;
	font-weight: normal;
	border-bottom: 1px dotted #999;
}
.hustle-two code,
.hustle-two pre {
	font: 400 13px/15px Consolas, Monaco, monospace;
	border-left: 2px solid rgba(0,0,0,0.2);
	padding: 5px 10px 5px 10px;
	background: rgba(0,0,0,0.05);
	border-radius: 3px;
	color: #555;
	margin: 1em 0;
	overflow: auto;
	vertical-align: middle;
}
.hustle-two code {
	display: inline-block;
	margin: 0;
}
.hustle-two b,
.hustle-two strong {
	font-weight: 500;
}
.hustle-two a {
	text-decoration: none;
	color: #19B4CF;
}
.hustle-two a:active,
.hustle-two a:focus,
.hustle-two a:hover {
	color: #477B92;
}
.hustle-two a.disabled,
.hustle-two a.disabled:active,
.hustle-two a.disabled:hover,
.hustle-two a.disabled:focus {
	color: 788084;
	cursor: default;
}

// 2. ELEMENT STATES
.hustle-two .block {
	display: block;
	width: 100%;
	float: none;
}
.hustle-two .button.block,
.hustle-two button.block,
.hustle-two input.block {
	text-align: center;
}
.hustle-two .space-t {
	margin-top: 20px;
}
.hustle-two .space-b {
	margin-bottom: 20px;
}
.hustle-two .tl {
	text-align: left;
}
.hustle-two .tc {
	text-align: center;
}
.hustle-two .tr {
	text-align: right;
}
.hustle-two .group {
	clear: both;
	margin: 0 0 30px;
}
.hustle-two .group:after {
	content: '';
	display: table;
	clear: both;
}
.hustle-two .input-group {
	display: inline-block;
	white-space: nowrap;
}
.hustle-two .float-r {
	float: right;
}
.hustle-two .float-l {
	float: left;
}
.hustle-two .pointer {
	cursor: pointer;
}
.hustle-two .dev-label {
	background: #ECECEC;
	border: 1px solid #E5E5E5;
	color: #BABABA;
	line-height: 30px;
	padding: 0 10px;
	border-radius: 3px;
	display: inline-block;
	vertical-align: middle;
	margin: 0 5px;
	cursor: default;
}
.hustle-two .can-close .box-title .close,
.hustle-two .can-close .close-icon {
	cursor: pointer;
	position: relative;
	display: inline-block;
	width: 22px;
	height: 22px;
	line-height: 22px;
	float: right;
}
.hustle-two .can-close .box-title .close {
	float: right;
	margin: 20px -10px 0 0;
}
.hustle-two .can-close .box-title .close:after,
.hustle-two .can-close .close-icon:after {
	content: "\79";
	font-family: 'WPMU-DEV-App-Icons';
	speak: none;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 22px;
	text-align: center;
	font-size: 14px;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	background: #FFF;
	color: #777771;
	transition: color 0.3s;
}
.hustle-two .can-close .box-title .close:hover:after,
.hustle-two .can-close .close-icon:hover:after {
	color: #333;
}
.hustle-two .loading.disabled:before,
.hustle-two .loading:before {
	content: '';
	background: transparent url(img/spin.gif) center center no-repeat;
	background-size: contain;
	display: inline-block;
	width: 18px;
	height: 18px;
	vertical-align: top;
	position: static;
	border: none;
	margin: 0;
	z-index: 1;
	opacity: 1;
}
.hustle-two .has-hover .show-on-hover {
	opacity: 0;
	transition-property: opacity;
	transition-duration: 0.3s;
}
.hustle-two .has-hover:hover .show-on-hover,
.hustle-two .has-hover .show-on-hover.loading {
	opacity: 1;
}