.hustle-two {
	.dev-box {
		border-radius: 5px;
		background-color: $color--white;
		box-shadow: 0 2px 0 #EAEAEA;
		padding: 30px;
		margin: 0 0 30px;
		.box-title {
			height: 60px;
			padding: 0 30px;
			border-bottom: 1px solid #EAEAEA;
			margin: -30px -30px 30px -30px;
			h3 {
				color: $color--montana;
				font: 700 18px/60px $font--alt;
				color: #777771;
				line-height: 60px;
				margin: 0;
				text-align: left;
				white-space: nowrap;
				overflow: hidden;
				text-overflow: ellipsis;
			}
			.buttons .dev-icon {
				font-size: 18px;
			}
		}
	}
	.box-footer {
		text-align: center;
	    margin-top: 32px;
	    font-size: 16px;
	}
	.box {
		margin: 0 0 30px;
		border-radius: 5px;
		background-color: $color--white;
		box-shadow: 0 2px 0 $color--lily;
		.box-title {
			position: relative;
			margin-bottom: -1px;
			padding: 0 30px;
			border-bottom: 1px solid $color--lily;
		}
		.box-content {
			padding: 0 15px;
		}
	}
}