.wph-modal.wph-modal--minimal {
	.wph-modal--content {
		border-radius: 5px;
		background: $white;
	}
	header {
		margin: 0;
		padding: 10px 20px;
		border-top-left-radius: inherit;
		border-top-right-radius: inherit;
		border-bottom: 1px solid rgba(0, 0, 0, 0.08);
		h2.wph-modal--title,
		h4.wph-modal--subtitle {
			color: $black;
			text-align: left;
		}
	}
	section {
		display: flex;
		flex-wrap: wrap;
		.wph-modal--image {
			flex: 0 200px;
			&_full {
				flex: 1;
			}
		}
		.wph-modal--message {
			flex: 1;
			padding: 20px;
			.wph-modal--cta {
				display: none;
			}
		}
		// Image: Align to left
		&.wph-modal--image_left {
			flex-direction: row;
			@media (max-width: 770px){
				flex-direction: column;
			}
		}
		// Image: Align to right
		&.wph-modal--image_right {
			flex-direction: row-reverse;
			@media (max-width: 770px){
				flex-direction: column-reverse;
			}
		}
	}
	footer {
		margin: 0;
		padding: 10px 20px;
		border-top: 1px solid rgba(0, 0, 0, 0.08);
		border-bottom-right-radius: inherit;
		border-bottom-left-radius: inherit;
		background: rgba(0, 0, 0, 0.08);
		text-align: right;
		a:not(:last-child) {
			margin-right: 20px;
		}
		a:not(.wph-modal--cta) {
			color: $green;
			&:hover {
				color: $green-dark;
			}
		}
	}
}

// Custom Size
.wph-modal.wph-modal--custom.wph-modal--minimal {
	footer {
		a.wph-modal--cta {
			overflow: hidden;
			text-overflow: ellipsis;
			white-space: nowrap;
		}
	}
}

// Image: Border Radius
.wph-modal.wph-modal--minimal {
	.wph-modal--content {
		// No Header ONLY
		&.wph-modal--noheader:not(.wph-modal--nofooter) {
			section {
				border-top-left-radius: inherit;
				border-top-right-radius: inherit;
				&.wph-modal--image_left {
					.wph-modal--image,
					.wph-modal--image img {
						border-top-left-radius: inherit;
						@media (max-width: 770px) {
							border-top-right-radius: inherit;
						}
					}
					.wph-modal--image_full,
					.wph-modal--image_full img {
						border-top-left-radius: inherit;
						border-top-right-radius: inherit;
					}
				}
				&.wph-modal--image_right {
					.wph-modal--image,
					.wph-modal--image img {
						@media (min-width: 770px) {
							border-top-right-radius: inherit;
						}
					}
					.wph-modal--image_full,
					.wph-modal--image_full img {
						border-top-left-radius: inherit;
						border-top-right-radius: inherit;
					}
				}
			}
		}
		// No Footer ONLY
		&.wph-modal--nofooter:not(.wph-modal--noheader) {
			section {
				border-bottom-left-radius: inherit;
				border-bottom-right-radius: inherit;
				&.wph-modal--image_left {
					.wph-modal--image,
					.wph-modal--image img {
						@media (min-width: 771px) {
							border-bottom-left-radius: inherit;
						}
					}
					.wph-modal--image_full,
					.wph-modal--image_full img {
						border-bottom-right-radius: inherit;
						border-bottom-left-radius: inherit;
					}
				}
				&.wph-modal--image_right {
					.wph-modal--image,
					.wph-modal--image img {
						@media (min-width: 771px) {
							border-bottom-right-radius: inherit;
						}
						@media (max-width: 770px) {
							border-bottom-right-radius: inherit;
							border-bottom-left-radius: inherit;
						}
					}
					.wph-modal--image_full,
					.wph-modal--image_full img {
						border-bottom-left-radius: inherit;
						border-bottom-right-radius: inherit;
					}
				}
			}
		}
		// No Header, No Footer
		&.wph-modal--noheader.wph-modal--nofooter {
			section {
				border-radius: inherit;
				&.wph-modal--image_left {
					.wph-modal--image,
					.wph-modal--image img {
						border-top-left-radius: inherit;
						@media (min-width: 771px) {
							border-bottom-left-radius: inherit;
						}
						@media (max-width: 770px) {
							border-top-right-radius: inherit;
						}
					}
					.wph-modal--image_full,
					.wph-modal--image_full img {
						border-radius: inherit;
					}
				}
				&.wph-modal--image_right {
					.wph-modal--image,
					.wph-modal--image img {
						@media (min-width: 771px) {
							border-top-right-radius: inherit;
							border-bottom-right-radius: inherit;
						}
						@media (max-width: 770px) {
							border-bottom-right-radius: inherit;
							border-bottom-left-radius: inherit;
						}
					}
					.wph-modal--image_full,
					.wph-modal--image_full img {
						border-radius: inherit;
					}
				}
			}
		}
	}
}